<template>
  <div>
    <div class="header">
      <Nav-bar></Nav-bar>
    </div>
    <div class="section">
      <router-view></router-view>
    </div>
    <div class='top' id='gototop' @click='gototop' v-show='showGoTop'>
      <img src="./assets/leftarrow.svg" alt="">
    </div>
  </div>
</template>

<style lang="scss">
*{
  padding:0;
  margin:0;
}
ul,ol{
  list-style: none;
}
html,body{
  cursor: url(data:image/png;base64,), default;
}
.top{
  width:4rem;
  height: 4rem;
  border-radius: 50%;
  position:fixed;
  right:3%;
  bottom:6%;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  img{
    width: 80%;
    height: 80%;
    transform: rotate(90deg);
  }
}

</style>

<script>
import NavBar from './components/NavBar.vue'

export default {
  components: {
    NavBar,
  },
  data() {
    return {
      showGoTop: false, // 添加一个数据属性来控制显示或隐藏
      isScrolling: false, // 添加一个数据属性来控制节流
      timer: null, // 添加一个数据属性来存储定时器
    };
  },
  methods:{
    disableScroll() {
      window.addEventListener('wheel', this.preventDefault, { passive: false });
    },
    enableScroll() {
      window.removeEventListener('wheel', this.preventDefault, { passive: false });
    },
    preventDefault(e) {
      e.preventDefault();
    },
    gototop(){
      if (this.isScrolling) return; // 如果正在滚动，则直接返回
      this.isScrolling = true; // 设置滚动标志为true
      this.disableScroll(); // 禁用滚轮事件
      this.$nextTick(() => {
        const gototop = document.getElementById('gototop')
        gototop.addEventListener('click',()=>{
            if (this.timer) {
              clearInterval(this.timer); // 清除之前的定时器
            }
            this.timer = setInterval(() => {
              const left = window.pageYOffset
              const step = Math.ceil((left - 0) / 10)
              window.scroll( 0, left - step)
              if (left === 0){
                  clearInterval(this.timer)
                  this.isScrolling = false; // 滚动结束后，重置滚动标志
                  this.enableScroll(); // 恢复滚轮事件
              }
            }, 30)
        })
      });
    },
    handleScroll() {
      this.showGoTop = window.pageYOffset > 100; // 根据滚动距离更新showGoTop的值
    }
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll); // 添加滚动事件监听器
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll); // 移除滚动事件监听器
  }
}
</script>





